import React, { useEffect, useState } from 'react';
import signInWithJWTCookie from './functions/signInWithJWTCookie';

const UserStatus = () => {
  const [user, setUser] = useState({ message: 'No user logged in.' })
  useEffect(() => {
    const getUserData = async () => {
      let user = await signInWithJWTCookie();
      console.log("user--> ", user);
      setUser(user);
    };
    getUserData();


  }, [])


  return (
       <p style={{textAlign:'right', paddingRight:'5px'}}>user: <span style={{ color: 'green' }}>{user.email}</span></p>
  );
};

export default UserStatus;